.button{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: white;
    border: .5px solid rgba(255, 255, 255, .6);
    text-decoration: none;
    letter-spacing: 6px;

    /* default size */
    width: 200px;
    height: 40px;

    transition: background-color .3s ease-out, color .3s ease-out, border-color .3s ease-out;
}

.button:hover{
    background-color: white;
    color: black;
    cursor: pointer;
    border-color: white;
}

.button.small {
    width: 136px;
    height: 36px;
}

.button.uppercase {
    text-transform: uppercase;
}

.button.ghost{
    background-color: transparent;
    text-decoration: underline;
    border: none;
}

.button .right-icon{
    width: 6px;
    height: 6px;
    border-top: 1px solid white;
    border-right: 1px solid white;
    transform: rotate(45deg);
}

.button.ghost:hover{
    color: black;
    cursor: pointer;
    background-color: white;
    text-decoration: none;
}

.button.ghost:hover .right-icon{
    border-color: black;
}
